.App {
    background: #c2c2c2;
    border-width: 4px;
    border-style: solid;
    border-right-color: #999;
    border-bottom-color: #999;
    border-top-color: white;
    border-left-color: white;
    padding: 16px;
}

.Header {
    background: #c0c0c0;
    border-width: 4px;
    border-style: solid;
    border-top-color: #7b7b7b;
    border-left-color: #7b7b7b;
    border-right-color: white;
    border-bottom-color: white;
    padding: 10px 12px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.Face {
    width: 52px;
    height: 52px;
    border-width: 4px;
    border-style: solid;
    border-top-color: white;
    border-left-color: white;
    border-right-color: #7b7b7b;
    border-bottom-color: #7b7b7b;
    display: flex;
    font-size: 35px;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}

.Face:active {
    border-top-color: #7b7b7b;
    border-left-color: #7b7b7b;
    border-right-color: white;
    border-bottom-color: white;
}

.Body {
    margin-top: 16px;
    border-width: 4px;
    border-style: solid;
    border-top-color: #7b7b7b;
    border-left-color: #7b7b7b;
    border-right-color: white;
    border-bottom-color: white;
    display: grid;
    grid-template-rows: repeat(9, 1fr);
    grid-template-columns: repeat(9, 1fr);
}