.Button {
    width: 30px;
    height: 30px;
    border-width: 4px;
    border-style: solid;
    border-top-color: white;
    border-left-color: white;
    border-right-color: #7b7b7b;
    border-bottom-color: #7b7b7b;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    font-weight: bold;
}

.Button:active {
    border-top-color: #7b7b7b;
    border-left-color: #7b7b7b;
    border-right-color: white;
    border-bottom-color: white;
}

.Button span {
    display: block;
    margin-left: 2px;
    font-size: 12px;
}

.Button.visible {
    border-color: #7b7b7b;
    border-width: 1px;
}

.Button.red {
    background: red;
}

.Button.visible.value-0 {
    background: #bdbdbd;
}

.value-1 {
    color: blue;
}

.value-2 {
    color: green;
}

.value-3 {
    color: red;
}

.value-4 {
    color: purple;
}

.value-5 {
    color: maroon;
}

.value-6 {
    color: turquoise;
}

.value-7 {
    color: black;
}

.value-8 {
    color: gray;
}